import React, { useEffect, useState } from 'react';
import useToken from "./useToken.mjs";

const PostloginNav = () => {
    const { token, setToken } = useToken();
    const [backendData, setBackendData] = useState([{}]);
    console.log("token:",token);

    useEffect(() => {
        fetch("/S").then(response => response.json()).then(data => {
            setBackendData(data);
        });
    }, []);
    return (
        <nav className="navbar navbar-expand bg-primary navbar-dark">
            <div className="container-fluid">
            <a className="navbar-brand" href="/">Home</a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNav">
                <ul className="navbar-nav">
                <li className="nav-item">
                    <a className="nav-link" href="/lobby">Create New Game</a>
                </li>
                <li className="nav-item">
                    <a className="nav-link" href="/joingame">Join Game</a>
                </li>
                </ul>
                <div className="collapse navbar-collapse justify-content-end" id="navbarNav">
                <a className="nav-link text-white" href="/profile">Hello, {token}</a>
                </div>
                </div>
            </div>
        </nav>
    );
};

export default PostloginNav;