import './App.css';
import React, { useEffect, useState } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Home from './components/Home.mjs';
import Game from './components/Game.mjs';
import PreloginNav from "./components/PreloginNav.mjs";
import Lobby from "./components/Lobby.mjs";
import Login from "./components/Login.mjs";
import Joingame from './components/Joingame.mjs';
import Profile from './components/Profile.mjs';
import PostloginNav from "./components/PostloginNav.mjs";
import useToken from "./components/useToken.mjs";
import LoggedHome from "./components/LoggedHome.mjs";
import io from 'socket.io-client';

async function checkLogin(data) {
  const formData = new URLSearchParams();
  Object.keys(data).forEach(key => formData.append(key, data[key]));
  return await fetch('/S', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    body: formData
  }).then(data => data.json());
}

const App = () => {
  const [socket, setSocket] = useState(null);

  useEffect(() => {
    const newSocket = io(`http://localhost:5000/`, {
      withCredentials: true
    });
    setSocket(newSocket);
    return () => newSocket.close();
  }, [setSocket]);
  const { token, setToken } = useToken();
  checkLogin({token});
  return (
    <div className="App">
      {(!token) ? (
                <PreloginNav/>
            )  : (
              <PostloginNav/>
            )}
      <br/><br/>
      <BrowserRouter>
        <Routes>
        {(!token) ? (
                <Route path='/' exact element={<Home/>} />
            )  : (
              <Route path='/' exact element={<LoggedHome/>} />
            )}
          <Route path='/game' exact element={<Game socket={socket}/>} />
          <Route path='/lobby' exact element={<Lobby socket={socket}/>} />
          <Route path='/login' exact element={<Login setToken={setToken}/>} />
          <Route path='/profile' exact element={<Profile/>} />
          <Route path='/joingame' exact element={<Joingame/>} />
        </Routes>
      </BrowserRouter>
    </div>
  )
}

export default App;