import React, { useEffect, useState } from 'react';
import { Navigate, useNavigate } from "react-router-dom";
import Messages from "./Messages.mjs";
import useToken from "./useToken.mjs";


const Lobby = ({socket}) => {
    const { token, setToken } = useToken();
    const [backendData, setBackendData] = useState([{}]);
    const [players, setPlayers] = useState([]);
    const navigate = useNavigate();
    // let notRendered = true;
    // if(socket && notRendered) {socket.emit("joinRoom"); notRendered = false;}
    useEffect(() => {
        const getPlayers = (backendData) => {
            const playersComp = backendData.game.players.map(player => {
                return <h5>{player.user.username}</h5>;
            });
            setPlayers(playersComp);
        }
        fetch(`/lobbyS${window.location.search}`).then(response => response.json()).then(data => {
            setBackendData(data);
            if (backendData?.game?.players) {
                getPlayers(backendData);
            }
        });
        if(socket){
            socket.on("gameStart", () => {
                navigate(`/game?roomno=${backendData.game.roomcode}&player=${token}`);
            });
            socket.on("roomJoined", () =>{
                fetch(`/lobbyS${window.location.search}`).then(response => response.json()).then(data => {
                    setBackendData(data);
                    getPlayers(backendData);
                });
            });

            socket.on("gameDeleted", () => {
                navigate("/");
            });

            return () => {
                socket.off('gameStart');
                socket.off("roomJoined");
                socket.off("gameDeleted");
              };
        }
    }, [socket, navigate, backendData, token, players]);

    const handleStartGame = () => {
        socket.emit("startGame", backendData.game.roomcode);
    }
    
    const handleDeleteLobby = () => {
        socket.emit("lobbyDeleted", backendData.game.roomcode);
    }

    const handleLeaveLobby = () => {
        socket.emit("LobbyLeft", backendData.game.roomcode);
        navigate(`/`);
    }

    return (
        <div>
            {(!token) ? (
                <Navigate to="/login"/>
            )  : (
                <></>
            )}

            {(backendData.error) ? (
                <Navigate to="/login"/>
            ) : (
                <></>
            )}
            
            {(!backendData.game) ? (
                <></>
            ) : (
                <div className="text-left">
                    <h1 id="room-name">Room Name: </h1> <h1 className="plain">{backendData.game.roomcode}</h1>
                    <h2 id="host">Host: {backendData.game.players[0].user.username}</h2>
                    <h2 id="users">Players:</h2> {players}
                    <br/>
                    <br/>
                    <button className="btn btn-primary btn-lg text-white" onClick={handleLeaveLobby}>Leave Lobby</button>
                    <br/>
                    {backendData.host &&
                        <div id="startGameForm">
                            <h3>Host Controls:</h3>
                            <button className="btn btn-primary btn-lg text-white" onClick={handleStartGame}>Start Game</button>
                            <button className="btn btn-primary btn-lg text-white" onClick={handleDeleteLobby}>Delete Lobby</button>
                        </div>
                    }
                </div>
            )}
        </div>
    );
};

export default Lobby;